export class XPTracker extends React.Component {
  render() {
    return <></>
    return (
      <>
        <svg xmlns="http://www.w3.org/2000/svg" width="auto" height={getTrackerHeight()} viewBox="0 0 294 84">
          <rect id="Rectangle_1474" data-name="Rectangle 1474" width="294" height="84" rx="30" fill="#f8f8f8" />
          <text id="XP" transform="translate(30 54)" font-size="33" font-weight="600"><tspan x="0" y="0">XP</tspan></text>
          <foreignObject x="90" y="25" width="160" height="160">
            <div style={{ width: "calc(30vh - 60px)" }}>
              <BorderLinearProgress variant="determinate" value={this.props.points} />
            </div>
          </foreignObject>
        </svg>

      </>
    )
  }
}
const BorderLinearProgress = withStyles((theme) => ({
  root: {
    height: 40,
    borderRadius: 20,
  },
  colorPrimary: {
    backgroundColor: "white",
  },
  bar: {
    borderRadius: 20,
    backgroundColor: '#8BEE7C',
  },
}))(LinearProgress);

export var updatePoints;

export class PointTrackers extends React.Component {
  constructor(props) {
    super(props);
    this.state = { xp: 0, watch_points: 0, collab_points: 0, fire_points: 0, answer_points: 0 }
    this.refreshPoints()
    updatePoints = () => {
      this.refreshPoints()
    }

  }
  refreshPoints = async () => {
    var newState = await getPoints()
    this.setState(newState)
  }
  render() {
    return (<>
      <div style={{ position: "fixed", top: "calc(2vh + 50px)" }}>
        <CashTracker points={this.state.points} />

      </div>
      <div style={(isMobile ? { position: "fixed", top: "calc(8vh + 50px)" }
        : { position: "fixed", top: "calc(2vh + 50px)", right: "15px" })}>
        <div style={{ display: "inline-block" }}>

          {this.props.journeyProgress ? <JourneyProgressTrackers points={this.props.journeyProgress} /> : null}
        </div>
        <div style={{ display: "inline-block", marginLeft: "20px" }}>
          <XPTracker points={this.state.xp} />
          <span style={{ display: "inline-block", marginRight: "20px" }}><JoinClassButton height={getTrackerHeight()} /></span>
          <span style={{ display: "inline-block" }}><FeedbackButton height={getTrackerHeight()} /></span>

        </div>
      </div>
    </>)
  }
}



class JourneyProgressTrackers extends React.Component {
  render() {
    return (
      <>
        <svg xmlns="http://www.w3.org/2000/svg" width="auto" height={getTrackerHeight()} viewBox="0 0 625 84">
          <rect id="Rectangle_1474" data-name="Rectangle 1474" width="625" height="84" rx="30" fill="#f8f8f8" />
          <text id="XP" transform="translate(30 54)" font-size="33" font-weight="600"><tspan x="0" y="0">
            Map Completion
          </tspan></text>
          <foreignObject x="300" y="25" width="235" height="160">
            <div style={{ width: "calc(45vh - 90px)" }}>
              <BorderLinearProgress variant="determinate" value={this.props.points} />
            </div>
          </foreignObject>
          <text id="XP" transform="translate(30 54)" font-size="33" font-weight="600"><tspan x="520" y="0">
            {this.props.points}%
          </tspan></text>
        </svg>

      </>
    )
  }
}