var greenInputStyle = {width: "300px", border: "1px solid #0fa", 
outline: "none",
mozBoxShadow: "0px 0px 15px rgba(0, 255, 170,.5)",
webkitBoxShadow: "0px 0px 15px rgba(0, 255, 170,.5)",
boxShadow: "0px 0px 15px rgba(0, 255, 170,.5)",}
var redInputStyle = {width: "300px", border: "1px solid #FF0033", 
outline: "none",
mozBoxShadow: "0px 0px 15px rgba(255, 90, 0,.5)",
webkitBoxShadow: "0px 0px 15px rgba(255, 90, 0,.5)",
boxShadow: "0px 0px 15px rgba(255, 90, 0,.5)",}

class AccessCodeField extends React.Component {
    constructor(props) {
        super(props)
        this.state = {}
    }
    checkAccessCode = async () => {
        //Backend not implemented yet
        this.setState({submitted: true})
        var validCode = await validateAccessCode(this.state.accessCode)
        if (validCode) {
            this.setState({correctAccessCode: true})
            setTimeout(() => {
                this.props.updateAccessCode(this.state.accessCode)
            }, 1000)
        }
        else {
            this.setState({correctAccessCode: false})
        }
        
    }
    render() {
        return (<div style={{ padding: "50px", borderRadius: "20px" }}>
        <h1 style={{ marginLeft: "40px" }}>Sign up</h1>
        {(this.state.correctAccessCode === false) ? <div style={{ color: "#FF0033" }}>Access Code invalid, try again</div>: null}
        <input style={this.state.correctAccessCode ? greenInputStyle: ((this.state.correctAccessCode === false) ? redInputStyle:{width: "300px"})} 
        onChange={(text) => { 
            this.setState({accessCode: text.target.value, correctAccessCode: null, submitted: null}) 
        }} 
        onKeyDown={(e) => {
            if (e.keyCode === 13) {
                this.checkAccessCode()
            }
            }}
        placeholder="Access Code"></input> 
        <button className="add-task-button" style={{marginTop: "30px",backgroundColor: colorTheme.primaryColor}} onClick={this.checkAccessCode}>
            <div className="add-button-text">
                Submit Access Code
            </div>
        </button>
        <SubmissionLoader submitted={this.state.submitted} correct={this.state.correctAccessCode}/>
    </div>)

        
    }
}


var changeErrorMessage
class LoginWidget extends React.Component {
    constructor(props) {
        super(props);
        this.state = { username: null, password: null, password2: null, showPassword: "password", errorMessage: null, email: null };
        changeErrorMessage = err => this.setState({ errorMessage: err })
    }
    updateUsername = (text) => {

        //errorMessage = null
        this.setState((props, state) => {
            return { username: text, password: this.state.password, errorMessage: null }
        })
    }
    updateEmail = (text) => {

        this.setState((props, state) => {
            return { email: text, errorMessage: null }
        })
    }
    updatePassword = (text) => {

        this.setState((props, state) => {
            return { username: this.state.username, password: text, errorMessage: null }
        })
    }

    updatePassword2 = (text) => {

        this.setState((props, state) => {
            return { password2: text, errorMessage: null }
        })
    }
    handleShowPassword = () => {
        //errorMessage = null
        var showPassword = "text";
        if (this.state.showPassword === "text") {
            showPassword = "password";
        }
        this.setState((props, state) => {
            return { username: this.state.username, password: this.state.password, showPassword: showPassword }
        })
    }



    renderShowPassword = () => {
        if (this.state.showPassword === "password") {
            return "show password";
        }
        else {
            return "hide password";
        }
    }
    
    render() {
        if (!this.state.accessCode){
        return <AccessCodeField updateAccessCode={(accessCode) => this.setState({accessCode: accessCode})}/>}
        return (<div style={{ padding: "50px", borderRadius: "20px" }}>
            <h1 style={{ marginLeft: "40px" }}>Sign up</h1>
            <div style={{ color: "red" }}>{this.state.errorMessage}</div>
            <input style={{ width: "300px" }} onChange={(text) => { this.updateUsername(text.target.value) }} placeholder="Username"></input>

            <div>
                <input style={{ width: "300px" }} onChange={(text) => this.setState({ first_name: text.target.value })} placeholder="First Name"></input>
            </div>
            <div>
                <input style={{ width: "300px" }} onChange={(text) => this.setState({ last_name: text.target.value })} placeholder="Last Name"></input>
            </div>
            <div>
                <div>
                    <input style={{ width: "300px" }} onChange={(text) => { this.updateEmail(text.target.value) }} placeholder="Email"></input>
                </div>
                <input style={{ width: "300px" }} onChange={(text) => { this.updatePassword(text.target.value) }} type={this.state.showPassword} placeholder="Password">
                </input></div>
            <div>
                <input style={{ width: "300px" }} onChange={(text) => { this.updatePassword2(text.target.value) }} type={this.state.showPassword} placeholder="Retype Password">
                </input></div>
            <div style={{ transform: "translateY(10px)" }}>

                <button onClick={this.handleShowPassword} style={{ cursor: "pointer" }}><p1>{this.renderShowPassword()}</p1></button>
                <div style={{ float: "right" }}><LoginButton info={{access_code:this.state.accessCode, username: this.state.username, first_name: this.state.first_name, last_name: this.state.last_name, email: this.state.email, password: this.state.password, password2: this.state.password2 }} /></div>
            </div>
        </div>);
    }
}

export class SignupPage extends React.Component {
    constructor(props) {
        super(props);
        getRequestToDoList()
    }
    render() {
        return (<div><div style={{ height: "100%", overflow: "hidden", margin: "0" }}>

            <div style={{
                position: "fixed",
                bottom: (isMobile ? "-10vh" : "-10px"),
            }}>
                <LoginSideSvg />

            </div>

        </div>
            {(isMobile ? null : <div style={{ marginLeft: "100px", transform: "translateY(100px)" }}>
                <img src={logo} style={(isMobile ? { height: "10vh" } : { height: "40vh" })} alt="Cambio logo" />
            </div>)}
            <div style={(isMobile ? { display: "flex", justifyContent: "center", alignItems: "center" } : { width: "400px", right: "100px", position: "absolute", bottom: "15vh" })}>
                <div style={{}}><LoginWidget /></div>
            </div>
            {(isMobile ? <div>
                <img src={logo} style={(isMobile ? { height: "20vh", position: "fixed", right: "25vw" } : { height: "40vh" })} alt="Cambio logo" />
            </div> : null)}
        </div>
        )
    }
}