import React from "react";
import { TeacherNavbar } from '../reused-components/navigation.js';
import { Link } from "react-router-dom";
import { PlussIcon, SkipBackwardIcon, SkipForwardIcon } from "../reused-components/icons.js";
import { TeacherOverviewList } from "../reused-components/TeacherComponents.js";
import axios from "axios";
import { getClassInfo, loadDownloadData } from "../backend.js";
import Switch from "react-switch";

class ChangeLessonButtons extends React.Component {
  constructor(props) {
    super(props)
    this.state = { currentLesson: "Loading..." }
    this.updateLesson()
  }
  updateLesson = async () => {

    await getClassInfo()
    var lessonData = await loadDownloadData()
    this.setState({ currentLesson: lessonData.description })
  }
  nextLesson = async () => {
    await axios.get("https://cambio-api.herokuapp.com/api/CoreFunctions/next_lesson/", {
      headers: {
        'Authorization': `token ${localStorage.getItem("token")}`
      }
    }).catch(err => console.log(err))
    await this.updateLesson()
  }
  previousLesson = async () => {
    await axios.get("https://cambio-api.herokuapp.com/api/CoreFunctions/previous_lesson/", {
      headers: {
        'Authorization': `token ${localStorage.getItem("token")}`
      }
    }).catch(err => console.log(err))
    await this.updateLesson()
  }
  render() {
    return <div style={{ margin: "1vw", marginBottom: "0", marginTop: "5vh" }}>
      <div style={{ backgroundColor: "#f4f4f4", borderRadius: "2vh", overflow: "hidden", display: "inline-block" }}>

        <div style={{ display: "inline-block", textAlign: "center", cursor: "pointer", borderRight: "2px solid #e8e8e8", padding: "1vh" }} className="dropdown-menu-item" onClick={this.previousLesson}>
          <SkipBackwardIcon size="4vh" />
          <div>
            Previous Lesson
          </div>
        </div>
        <div style={{ display: "inline-block", textAlign: "center", cursor: "pointer", padding: "1vh" }} className="dropdown-menu-item" onClick={this.nextLesson}>
          <SkipForwardIcon size="4vh" />
          <div>
            Next Lesson
          </div>
        </div>


      </div>
      <div style={{ marginTop: "1vh", fontWeight: "600", fontSize: "15px" }}>
        Current Lesson: {this.state.currentLesson}
      </div>
    </div>
  }
}

export class TeacherClassPage extends React.Component {
  constructor(props) {
    super(props)
    this.state = {}
  }
  render() {
    return (
      <div>
        <TeacherNavbar />
        <div style={{ paddingTop: "3vh", paddingLeft: "9vw", position: "relative" }}>
          <Link to="/teacher/class/assignments" style={{ textDecoration: "none" }}>
            <h1 style={{ cursor: "pointer", color: "black" }}>Assignments <OpenInFullIcon /></h1>
          </Link>
          <div style={{ height: "50vh", width: "50vw", display: "inline-block" }}>
            <TeacherOverviewList type="lesson" link="/teacher/class/" />
          </div >
          {/* No use for this currently
            <div style={{position: "fixed", bottom: "12vh", display: "flex", alignItems: "center", cursor: "pointer", fontWeight: 600,
          fontSize: "3vh"
          }}>
            <PlussIcon size={"5vh"}/> <div style={{marginLeft: "1.5vw"}}>Create Assignments</div>
        </div> */}
          <div style={{ position: "absolute", right: "7vw", top: "3vh" }}>
            <div style={{ borderLeft: "3px solid #bbb", position: "fixed", right: "36vw", height: "70vh", marginTop: "10vh" }}></div>
            {!this.state.group ? <><Link to="/teacher/class/students" style={{ textDecoration: "none" }}>
              <h1 style={{ cursor: "pointer", color: "black" }}
              >Students <OpenInFullIcon />
              <span style={{marginLeft: "20px", position: "absolute", transform: "translatey(5px)"}} >
                <StudentGroupSwitch checked={this.state.group}
                changeCheckedType={(checked) => {
                  this.setState({group: checked})
                }}
                />
                </span>
              </h1>
            </Link>
              <div style={{ height: "50vh", width: "25vw" }} key={this.state.group}>
                <TeacherOverviewList type="student" link="/teacher/class/" notClickable={true} givePoints={true} />
              </div></> :
              <>
              <h1 style={{ cursor: "pointer", color: "black" }}
              >
                
                Groups 
                <span style={{marginLeft: "20px", position: "absolute", transform: "translatey(5px)"}}>
                <StudentGroupSwitch checked={this.state.group}
                changeCheckedType={(checked) => {
                  this.setState({group: checked})
                }}
                />
                </span>
              </h1>
              <div style={{ height: "50vh", width: "25vw" }} key={this.state.group}>
                <TeacherOverviewList type="group" link="/teacher/class/" givePoints={true}/>
              </div></>}
            {// <CheckpointButton /> No use for this currently
            }
            <ChangeLessonButtons />
          </div>
        </div>
      </div>
    )
  }
}



class StudentGroupSwitch extends React.Component {
  constructor() {
    super();
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(checked) {
    this.props.changeCheckedType(checked)
    this.setState({ checked });
  }

  render() {
    return (        
          <Switch onChange={this.handleChange} checked={this.props.checked}
            uncheckedIcon={false}
            checkedIcon={false}
            height={30}
            width={60}
            onColor="#48E23D"
          />
        
    );
  }
}


