function removeNumberFromString(s) {
  var newS = ""
  for (var i = 0; i < s.length; i++) {
    if (isNaN(s[i])) {
      newS += s[i]
    }
  }
  return newS
}

export class JourneyPage extends React.Component {
  constructor(props) {
    super(props)
    this.state = { isExtended: "extended", availableJourneyMapLessons: 0 }
    this.checkLessonAvailability()
  }
  changeState = () => {
    if (this.state.isExtended === "extended") {
      this.setState({ isExtended: "not-extended" })
    }
    else {
      this.setState({ isExtended: "extended" })
    }
  }
  checkLessonAvailability = async () => {
    var availableJourneyMapLessons = await getAvailableJourneyMapLessons()
    this.setState({ availableJourneyMapLessons: availableJourneyMapLessons })
  }
  render() {
    var journeyProgress = Math.round(this.state.availableJourneyMapLessons * 100 / 19).toString()
    return (
      <>
        <div style={{ position: "absolute" }}>
          <Navbar pageName="Your Journey" sideNavAnimation={this.changeState} />
        </div>
        <div style={{ position: "absolute", zIndex: -1 }} className={this.state.isExtended}>
          <div style={{ display: "inline-block", position: "absolute" }}>
            <SideNav isExtended={"extended"} />
          </div>
          <div className="todo-outer-div" style={((this.state.isExtended === "extended") ? {} : { width: "100vw" })}>
            <div style={{ marginLeft: "40px" }}>
              <PointTrackers journeyProgress={journeyProgress} />
            </div>
            <div style={{ height: "75%", width: "90%", marginTop: "3%", marginLeft: "-5%", position: "fixed", right: "-15%" }} className="scrollX">
              <div style={
                { height: "100%", width: 
                //[null, "calc(400vh)", "calc(260vh)", "calc(260vh)" ][journeyMapVersion]
                {"CSH":"calc(400vh)", "OFA": "calc(260vh)", "IFE": "calc(260vh)"}[journeyMapVersion]
                }}>
                {
                  {"CSH": <JourneyMap />, "OFA": <JourneyMapFullVersion />, "IFE": <JourneyMapIFE />}[journeyMapVersion]
                  //[null, <JourneyMap />, <JourneyMapFullVersion />, <JourneyMapIFE />][journeyMapVersion]
                }
              </div>
            </div>


          </div>
          <div>
          </div>
        </div>
      </>
    )
  }
}
