export class CheckpointPopUp extends React.Component {
  constructor(props) {
    super(props)
    this.state = { availableLessons: (this.props.checkpointUnlocked ? this.props.availableLessons : 0), currentCheckpointLessons: [] }
    this.checkIfGuest()
    this.checkIfTeacher()
    this.updateLessons()
  }
  checkIfTeacher = async () => {
    var isTeacher = await getIsTeacher()
    if (isTeacher) {
      this.setState({availableLessons: 600})
    }
  }
  checkIfGuest = async () => {
    if (await isGuest()) {
      if (this.props.checkpoint === 1) {
        this.setState({ availableLessons: 1 })
      }
      else {
        this.setState({ availableLessons: 0 })
      }
    }
  }
  updateLessons = async () => {
    var mapLessons = await getMapLessons()
    //alert(JSON.stringify(mapLessons))
    //alert(this.props.currentCheckpoint)
    if (mapLessons.length < this.props.checkpoint) {
      this.setState({currentCheckpointLessons: []})
      return
    }
    this.setState({currentCheckpointLessons: mapLessons[this.props.checkpoint -  1]})
  }
  render() {
    return (
      <div className="popup-background-div">
        <div className="popup-clickable-background-div" onClick={this.props.changePopUp}></div>
        <div style={{ minWidth: "30vw", zIndex: 2 }}>
          <div style={{ minWidth: "30vw", backgroundColor: "#8b4389", opacity: "100%", zIndex: 2, borderRadius: "20px 20px 0 0 ", paddingBottom: "5px", paddingTop: "5px" }}>
            <button style={{ cursor: "pointer", float: "right", marginTop: "15px", marginRight: "15px" }} onClick={this.props.changePopUp}>
              <CloseIcon color="white" /></button>
            <h1 style={{ marginLeft: "20px", color: "white" }}>Lessons</h1></div>
          <div style={{ minWidth: "30vw", backgroundColor: "white", opacity: "100%", zIndex: 2, borderRadius: "0 0 20px 20px ", paddingBottom: "50px", paddingTop: "50px" }}>
            <div style={{ marginLeft: "30px" }}>
              {
                this.state.currentCheckpointLessons.map((lesson, index) => <div style={{ cursor: "pointer", opacity: ((this.state.availableLessons <= index) ? 0.5 : 1) }}
                  onClick={((this.state.availableLessons <= index) ? null : () => {
                    if (!lesson.lesson_id) {
                      window.location.href = "/#/"
                      window.location.reload()
                    }
                    changeLessonHomepage(lesson.lesson_id)
                    window.location.href = "/#/"
                  })}
                >
                  <LessonIcon />  <h1 style={{ display: "inline-block", fontSize: "20px", transform: "translateY(-5px)" }}>{lesson.lesson_name}</h1>
                </div>)}

            </div>
          </div>
        </div>

      </div>

    )
  }
}





