class Checklist extends React.Component {
  constructor(props) {
    super(props)
    this.state = { open: false }
  }
  changeChecklist = () => {
    this.setState({ open: !this.state.open })
  }
  render() {
    return (
      <div style={{ position: "fixed", right: "20px", bottom: "20px" }}>
        <div className={this.state.open ? "journey-checklist-open" : "journey-checklist"} >
          <div onClick={this.changeChecklist} style={{ width: "calc(100% - 20px)", backgroundColor: "#8b4389", opacity: "100%", zIndex: 2, borderRadius: "10px 10px 0 0 ", padding: "10px" }}><div style={{ display: "inline-block", color: "white" }}>Checklist</div> <div style={{ float: "right", transform: (this.state.open ? "rotate(180deg)" : "rotate(0deg)") }}><ExpandIcon /></div></div>
          <div style={{ paddingLeft: "10px" }}>
            {["1 Pager", "Pitch Deck", "Prototype", "Startup Resume", "Business Plan"].map(text => <div><input style={{ height: "15px", width: "15px", borderRadius: "5px" }} type="checkbox"
              defaultChecked={true} />{text}</div>)}
          </div>
        </div></div>
    )
  }
}
export async function getCheckPointsAndLessons(totalAvailableLessons) {
  var counter = 0
  var checkpoint
  var availableLessons
  var mapLessons = await getMapLessons()
  
  mapLessons.forEach((lessonList, index) => {
    lessonList.forEach((lesson, ind) => {
      counter++
      if (totalAvailableLessons == counter) {
        checkpoint = index + 1
        availableLessons = ind + 1
      }
    })

  });
  
  return [checkpoint, availableLessons]
}


