export function updateToDoList(list) {
    user.toDoList = list;
    var tempList = [];
    list.forEach(lst => {
        var tempLst = [];
        lst.forEach(item => {
            tempLst.push(item.content + "//>" + JSON.stringify(item.assignedUser) + "//>" + item.difficulty + "//>" + item.hours + "//>" + item.dueDate + "//>" + JSON.stringify(item.actionSteps))
        })
        tempList.push(tempLst)
    })

    axios.patch(`https://cambio-api.herokuapp.com/api/Groups/${localStorage.getItem("group")}/`, ({
        backlog: tempList[0],
        to_do: tempList[1],
        doing: tempList[2],
        done: tempList[3],
    }), {
        headers: {
            'Authorization': `token ${localStorage.getItem("token")}`
        }
    })
    //returns nothing
}

export function isLogin() {
    return JSON.parse(localStorage.getItem("loggedIn"));
}
export async function login(username, password) {
    var isLoggedIn;
    const config = {
        headers: {
            'Content-Type': 'application/json',
        },
    };
    await axios.post("https://cambio-api.herokuapp.com/api/auth/login", { username: username, password: password }, config).then(
        async res => {
            localStorage.setItem("loggedIn", true)
            localStorage.setItem("token", res.data.token)
            localStorage.setItem("username", res.data.user.username)
            localStorage.setItem("user", JSON.stringify(res.data))
            //console.log(res.data)
            await getUserInfo()
            isLoggedIn = true
            localStorage.setItem("loginInfo", JSON.stringify([username, password]))
        }
    ).catch(err => {
        isLoggedIn = false
    })
    return isLoggedIn;

}

