export async function signUp(info) {
    var success = false
    await axios.post("https://cambio-api.herokuapp.com/api/auth/register", info).then(res => {
        console.log("success")
        localStorage.setItem("loggedIn", res.data.token !== null)
        localStorage.setItem("token", res.data.token)
        localStorage.setItem("user", JSON.stringify(res.data))
        success = true
    }
    ).catch(
        (err) => {
            console.log(err)
        }
    )
    return success
}

