export async function uploadMasterAssignment(file, checkpoint, fileName, fileURL, challengeID) {
    var successfulUpload = false;
    var formData = new FormData()
    if (file) {
        formData.append("file", file)
    }
    formData.append("checkpoint", checkpoint)
    formData.append("file_name", fileName)
    //formData.append("challenge_name", challengeName)
    formData.append("challenge_id", challengeID)
    if (fileURL) {
        formData.append("url_submissions", fileURL)
    }
    await axios.post("https://cambio-api.herokuapp.com/api/AssignmentUpload/", formData, {
        headers: {
            'Authorization': `token ${localStorage.getItem("token")}`
        }
    }).then((response) => {
        console.log(response);
        successfulUpload = true
    })
        .catch((error) => {
            console.log({ file: file, checkpoint: checkpoint, file_name: fileName, url_submissions: fileURL, challenge_id: challengeID })
            console.log(error);
            successfulUpload = false

        });
    return successfulUpload
}
