export var getUserInfo = async () => {
    var userData;
    if (!localStorage.getItem("token")) {
        return
    }
    await axios.get(`https://cambio-api.herokuapp.com/api/Students/${localStorage.getItem("username")}/`, {
        headers: {
            'Authorization': `token ${localStorage.getItem("token")}`
        }
    })
        .then((response) => {
            console.log(response)
            isTeacher = response.data.is_teacher
            localStorage.setItem("group", response.data.group_name)
            user.cash = response.data.cash
            user.xp = response.data.xp
            userData = response.data
            userCash = userData
            username = response.data.name
            fullName = response.data.first_name1 + " " + response.data.last_name1
        })
        .catch((error) => {
            console.error(error)
            logout()
            window.location.reload()
        })
    return userData
}

