export async function getClassInfo() {


    var students = [];
    var userData = await getUserInfo()
    if (!userData) {
        return;
    }
    await axios.get(`https://cambio-api.herokuapp.com/api/School_Class/${userData["class_name"]}/`, {
        headers: {
            'Authorization': `token ${localStorage.getItem("token")}`
        }
    }).then(
        async res => {
            classGroups = res.data.groups
            checkpointList = res.data.checkpoint_list
            students = res.data.students
            console.log(res.data)
            checkpoint = res.data.current_lesson_id
            courseVersion = res.data.courseVersion
            getMapVersion()
            availableJourneyMapLessons = res.data.lessons_activated
            className = res.data.class_name
            if (await isGuest()) {
                checkpoint = 5
            }
        }
    ).catch(res => {
        console.log(res.message)
        if (res.message === "Request failed with status code 403") {

            return []
        }
    }
    )
    return students
}

