import React from "react"
import Loader from "react-loader-spinner";
import ReactPlayer from 'react-player/lazy'
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";

export class JourneyVideoPlayer extends React.Component {
    constructor(props) {
        super(props)
        //this.state = this.props
    }
    getEmbedUrl(url) {
        var video_id = url.split('v=')[1];
        if (video_id === undefined) {
            video_id = url.substring(url.length-11,url.length)
        }
        var ampersandPosition = video_id.indexOf('&');
        if (ampersandPosition != -1) {
            video_id = video_id.substring(0, ampersandPosition);
        }
        //alert(video_id)
        return `https://www.youtube.com/embed/${video_id}`;
    }
    
    render() {
        return (this.props.src ?
            (this.props.src.substr(-3) === "png" ? <img src={this.props.src} height="100%" width="100%"/> : <ReactPlayer url={this.props.src} width="100%" height="100%" controls={true}
            
            />)
        /*<iframe src={this.getEmbedUrl(this.props.src)} height="100%" width="100%" frameborder="0" allowfullscreen="allowfullscreen"
        mozallowfullscreen="mozallowfullscreen" 
        msallowfullscreen="msallowfullscreen" 
        oallowfullscreen="oallowfullscreen" 
        webkitallowfullscreen="webkitallowfullscreen"></iframe>*/
        : <div style={{
          width: "100%", height: "100%", display: "flex",
          justifyContent: "center", alignItems: "center", backgroundColor: "black"

        }}><div style={{ height: "25%", width: "15%" }}><Loader type="Oval" color="#00BFFF" height={"100%"} width={"100%"} />

          </div></div>)
    }
}