import React from "react";
import {CloseIcon,  HatchetIcon, CheckIcon} from "../../reused-components/icons.js";
import {AssignmentStatus} from "./ChallengePopUp.js";
import "../../style.css"
import { closePopup, openPopup } from "../../reused-components/Popup.js";
import { VideoQuestionPopup } from "../../VideoQuestionPopup.js";
import { checkIfPromptCompleted } from "../../backend.js";
import { ViewPromptForDiscussionPopUp } from "./ViewPromptForDiscussionPopUp.js";
import { colorTheme } from "../../reused-components/colorTheme.js";

export class PromptOverviewPopUp extends React.Component {
    constructor(props) {
        super(props)
        this.state = {prompts:this.props.downloadData.prompts.length > 0 ? this.props.downloadData.prompts : JSON.parse(this.props.downloadData.video_download_prompts) }
        this.checkPromptStatus()
    }
    checkPromptStatus = async () => {
      var promptStatuses = await checkIfPromptCompleted()
      var prompts = this.state.prompts
      promptStatuses.forEach(status => {
        prompts.forEach((prompt, index) => {
          //alert(JSON.stringify(status))
          if (status.promptName === prompt.title) {
            
            prompts[index].status = status.status
          }
        })
      })
      //alert(JSON.stringify(prompts))
      this.setState({prompts: prompts})
    }
    renderPrompts = () => {
      //return JSON.stringify(this.state.prompts)
        if (this.state.prompts.length == 0) {
          return <div>
            None
          </div>
        }
        return this.state.prompts.map((prompt) => {
            
          return (<div style={{ padding: "10px", borderRadius: "10px", cursor:"pointer", width: "100%", display: "flex", aligItems: "center"}} className="darken"
          onClick={() => {
            //this.props.changePopUp()
            if (["approved", "submitted", "failed"].includes(prompt.status)) {
              //closePopup()
              //this.props.changePopUp()
              openPopup(<ViewPromptForDiscussionPopUp promptID={prompt.id} promptQuestion={prompt.question}/>)
              return
            }
            if (prompt.TimerLength) {
              var tempPrompt = prompt
              prompt = {type: "timedPrompt"}
              prompt.prompts = [tempPrompt]
            }
            openPopup(<VideoQuestionPopup 
              changePopUp={() => {
                closePopup()
              }} videoQuestionData={prompt}/>)
        }}
          >
            <AssignmentStatus status={prompt.status}/><p1 style={{ marginLeft: "15px", marginRight: "10px"}}>
              {prompt.title} {"(3 points)"}</p1>
              {//Add ability for students to view submitted answers later
              }
              {(["approved", "submitted", "failed"].includes(prompt.status)) ? 
              <button className="add-task-button" style={{marginLeft: "auto", width: "170px",backgroundColor: colorTheme.primaryColor}}><div className="add-button-text" style={{width: "160px"}}>View for Discussion</div></button>:
               <button className="add-task-button" style={{marginLeft: "auto", width: "130px",backgroundColor: colorTheme.primaryColor}}><div className="add-button-text">View Prompt</div></button>}

              </div>)
        
        })
    }
    render() {
        return <div style={{minWidth: "30vw", zIndex: 2, maxWidth: "70vw"}} key={JSON.stringify(this.state.prompts)}>
        <div style={{ minWidth: "30vw", backgroundColor: colorTheme.primaryColor, opacity: "100%", zIndex: 2, borderRadius: "20px 20px 0 0 ", paddingBottom: "5px", paddingTop: "5px" }}>
          <button style={{ cursor: "pointer", float: "right", marginTop: "15px", marginRight: "15px" }} onClick={closePopup}>
            <CloseIcon color="white"/></button>
          <h1 style={{ marginLeft: "20px", color: "white" }}>
              Prompts Overview
              </h1>
              </div>
        <div style={{ minWidth: "30vw", backgroundColor: "white", opacity: "100%", zIndex: 2, borderRadius: "0 0 20px 20px ", paddingBottom: "30px", paddingTop: "50px" }}>
          <div style={{marginLeft: "30px", paddingRight: "30px"}}>
              {this.renderPrompts()}
            </div>
          </div>
        </div>
    }
}