import React from "react";
import { TeacherNavbar } from '../reused-components/navigation.js';
import { getTurnedInMasterAssignments, getMasterDownloadLinks, updateMasterAssignments, getAssignmentSubmission, getAssignmentById, submitAssignmentFeedback, approvePrompt, rejectPrompt } from "../backend.js"
import { ProfileIcon, DownloadIcon, CloseIcon } from "../reused-components/icons.js";
import { useHistory } from "react-router-dom";
import { Redirect, Route } from "react-router-dom"
import Slider from 'react-input-slider';
import { AssignmentStatus, openInNewTab } from "./HomePage/ChallengePopUp.js";
import { currentStudent, currentAssignment, currentSubmissionData, currentSubmissionList, currentSubmissionListID, increaseCurrentSubmissionListID } from "../reused-components/TeacherComponents.js";
import { openPopup, closePopup } from "../reused-components/Popup";
import { updatePoints } from "../reused-components/pointTrackers.js";


var refreshCurrentSubmission

export class TeacherSinglePromptPage extends React.Component {
  constructor(props) {
    super(props)
    this.studentOrTeacher = "t"
    //this.state = { comments: [], currentComment: "", grade: null, assignmentId: null, assignmentName: null, key:true }
    var submission = currentSubmissionList[currentSubmissionListID]
    if (!submission) {
      window.location.replace("#/teacher/class/assignment")
      return
    }
    this.state = submission
    this.state.points = 3
    refreshCurrentSubmission = this.getSubmissionInfo
  }
  getSubmissionInfo = async () => {
    //var submission = await getAssignmentSubmission(currentStudent, currentAssignment)
    var submission = currentSubmissionList[currentSubmissionListID]
    if (!submission) {
      window.location.replace("#/teacher/class/assignment")
      return
    }
    this.setState(submission)
  }
  renderPromptAnswers() {
    return <div style={{marginTop: "50px", fontSize: "20px"}}>
      {this.state.prompt_answer}
    </div>

  }
  render() {
    return (
      <div key={this.state.key}>
        <TeacherNavbar />
          
            <div style={{ margin: "100px", height: "100%"}}>
              <h1>
                {this.state.prompt_name1}
              </h1>
              {this.renderPromptAnswers()}
              <div style={{position: "absolute", bottom: "100px", padding: "30px", borderRadius: "20px", backgroundColor: "#f8f8f8"}}>
                <div style={{marginLeft: "30px", marginBottom: "30px"}}>
                  <AssignmentPointsSlider points={this.state.points} changePoints={points => this.setState({points: points})}/>
                  </div>
              
              <span style={{marginLeft: "30px"}}>
                <button className="add-task-button" style={{backgroundColor: "#30e024"}}
                onClick={async () => {
                  var successfulApproval = await approvePrompt(this.state.id, this.state.points)
                  if (successfulApproval) {
                    openPopup(<FeedbackSubmissionConfirmationPopUp />)
                  }}}>
                  <div className="add-button-text">
                    Give Points
                    </div>
                </button>
                </span>
                <span style={{marginLeft: "30px"}}>
                <button className="add-task-button" style={{backgroundColor: "#e02424"}}
                onClick={async () => {
                  var successfulApproval = await rejectPrompt(this.state.id)
                  if (successfulApproval) {
                    openPopup(<FeedbackSubmissionConfirmationPopUp />)
                  }
                }}>
                  <div className="add-button-text">
                    Reject Prompt
                    </div>
                </button>
                </span>
              </div>

            
          </div></div>)
  }
}


class AssignmentPointsSlider extends React.Component {
  constructor(props) {
    super(props)
    this.state = { pointValue: this.props.points * 33 }
  }
  render() {
    return (
      <div style={{ position: "relative"}}>
        <div style={{ fontWeight: 600, fontSize: 20 }}>
          Score ({this.props.points}/3)
        </div>
        <div style={{ marginTop: "20px" }}>
          <Slider
            axis="x"
            styles={{
              track: { width: 300, height: 25, borderRadius: 40 },
              active: { borderRadius: 40 },
              thumb: { width: 30, height: 30 }
            }}
            x={this.state.pointValue}
            onChange={({ x }) => {
              this.props.changePoints(Math.floor(x / 33))
              this.setState({ pointValue: x })
            }}
          /></div>
      </div>

    )
  }
}

class FeedbackSubmissionConfirmationPopUp extends React.Component {
  render() {
    return (<div style={{ padding: "15px", borderRadius: "20px", backgroundColor: "white", zIndex: 2 }}>

      Status has been updated!
      <div style={{ marginTop: "10px" }}>
        <button
          className="add-task-button"
          onClick={() => {

            closePopup()
            if (currentSubmissionListID === currentSubmissionList.length - 1) {
              window.location = "#/teacher/class/prompt/"
            }
            else if (!(currentSubmissionList[currentSubmissionListID + 1].status === "submitted")) {
              window.location = "#/teacher/class/prompt/"
            }
            else {
              increaseCurrentSubmissionListID()
              refreshCurrentSubmission()
            }
          }}
        >
          <div className="add-button-text">Okay</div>
        </button>
      </div>
    </div>)
  }
}