import React from "react";
import Loader from "react-loader-spinner";

export class SubmissionLoader extends React.Component {
    constructor(props) {
        super(props)
        this.size = this.props.size
        if (!this.size) {
            this.size = "30"
        }
    }
    render() {
        return <span key={JSON.stringify(this.state)}>
        {this.props.submitted ? 
        <span style={{display: "inline-block", marginLeft: "20px", transform: "translateY(10px)"}}>{this.props.correct? <svg xmlns="http://www.w3.org/2000/svg" width={this.size} height={this.size} viewBox="0 0 30 30">
  <path id="Path_392" data-name="Path 392" d="M17,2A15,15,0,1,0,32,17,15.005,15.005,0,0,0,17,2ZM14,24.5,6.5,17l2.115-2.115L14,20.255,25.385,8.87,27.5,11Z" transform="translate(-2 -2)" fill="#0fa"/>
</svg>
 : 
        ((this.props.correct === false) ? <svg xmlns="http://www.w3.org/2000/svg" width={this.size} height={this.size} viewBox="0 0 30 30">
        <path id="Path_394" data-name="Path 394" d="M17,2A15,15,0,1,0,32,17,14.986,14.986,0,0,0,17,2Zm7.5,20.385L22.385,24.5,17,19.115,11.615,24.5,9.5,22.385,14.885,17,9.5,11.615,11.615,9.5,17,14.885,22.385,9.5,24.5,11.615,19.115,17Z" transform="translate(-2 -2)" fill="#f03"/>
      </svg>
      :<Loader type="Oval" color="#00BFFF" height={this.size} width={this.size} />)}
        </span>: null }
        </span>
    }
}