import { openPopup, closePopup } from "../../reused-components/Popup";
import { CloseIcon, DCMButton } from "../../reused-components/icons";
import React from "react";
import ReactPlayer from "react-player";
import { openInNewTab } from "./ChallengePopUp";
import { getTeacherToolsData } from "../../backend";
import { JourneyVideoPlayer } from "../../reused-components/JourneyVideoPlayer";
import { DownloadPopUp } from "../../HomePage";
import { colorTheme } from "../../reused-components/colorTheme";

export class TeacherToolsPopUp extends React.Component {
    constructor(props) {
        super(props);
        this.state = { tips: [], tipsTitles: [] }
        this.getTeacherTools()
    }
    getTeacherTools = async () => {
        var newState = await getTeacherToolsData()
        newState.tipsTitles = []
        for (var i = 0; i < newState.tips.length; i++) {
            var tip = newState.tips[i]
            var title = tip
            await fetch(tip)
                .then(function (response) {
                    return response.text();
                })
                .then(function (body) {
                    title = body.split('<title>')[1].split('</title>')[0]
                })
            newState.tipsTitles.push(title)
        }
        this.setState(newState)
    }
    render() {
        return (
            <div style={{ minWidth: "30vw", zIndex: 2 }}>
                <div style={{ minWidth: "30vw", backgroundColor: colorTheme.primaryColor, opacity: "100%", zIndex: 2, borderRadius: "20px 20px 0 0 ", paddingBottom: "5px", paddingTop: "5px" }}>
                    <button style={{ cursor: "pointer", float: "right", marginTop: "15px", marginRight: "15px" }} onClick={() => {
                        closePopup()
                    }}>
                        <CloseIcon color="white" /></button>
                    <h1 style={{ marginLeft: "20px", color: "white" }}>Teacher Tools</h1></div>
                <div style={{ minWidth: "30vw", backgroundColor: "white", opacity: "100%", zIndex: 2, borderRadius: "0 0 20px 20px ", paddingTop: "50px", paddingBottom: "20px" }}>
                    <div style={{ margin: "40px", marginTop: 0 }}>
                        {this.state.video_tips ? <div style={{ height: "40vh", aspectRatio: "16/9" }}>
                            {//<ReactPlayer url={this.state.video_tips} height="100%" width="100%" />
                            }
                            <JourneyVideoPlayer src={this.state.video_tips} />
                        </div> : null}

                        <div style={{ marginTop: "30px", justifyContent: (this.state.tips.length ? "space-evenly" : null), display: "flex", width: "250px" }}>
                            {this.state.lesson_plan ? <div style={{ cursor: "pointer" }} onClick={() => openInNewTab(this.state.lesson_plan)}>
                                <DCMButton text="Lesson Plan" />
                            </div> : null}
                            {this.state.tips.length ? <div style={{ cursor: "pointer" }} onClick={() => {
                                if (this.state.tips.length === 1) {
                                    openInNewTab(this.state.tips[0])
                                }
                                else {
                                    openPopup(<DownloadPopUp downloadData={{
                                        download_titles: this.state.tipsTitles, download_urls:
                                            this.state.tips
                                    }}
                                        changePopUp={() => { }}
                                    title="Tips"
                                    icon={<></>}
                                    />
                                    )
                                }
                            }}>
                                <DCMButton text="Tips" icon={null} narrowBrowser={this.narrowBrowser} />
                            </div> : null}

                        </div>
                    </div>

                </div>
            </div>
        )
    }
}