import React from "react";

export var closePopup;
export var openPopup;

export class Popup extends React.Component {
    constructor(props) {
        super(props)
        this.state = {visible: false, popups: []}
        closePopup = () => {
            var popups = this.state.popups
            popups.pop()
            if (popups.length > 0) {
                this.setState({popups: popups})
            }
            else {
                this.setState({visible: false})
            }
        }
        openPopup = (popup) => {
            var popups = this.state.popups
            popups.push(popup)
            this.setState({popups: popups, visible: true,popup:popup})
        }
    }
    render() {
        
        if (!this.state.visible) {
            return <></>;
        }
        return (
            <div className="popup-background-div">
        <div className="popup-clickable-background-div" onClick={closePopup}> 
        </div>
        {this.state.popups[this.state.popups.length-1]}
        </div>
        )
    }
}

