var mobileVersion = isMobile


export class AssignmentStatus extends React.Component {
  render() {
    var status = this.props.status
    if (status === "approved") {
      return <FinishedAssignmentIcon size={this.props.iconSize} />
    }
    else if (status === "failed") {
      return <FailedAssignmentIcon size={this.props.iconSize} />
    }
    else if (status === "submitted") {
      return <PendingAssignmentIcon size={this.props.iconSize} />
    }
    else {
      return <QuestionIcon />
    }
  }
}

export function findAssignmentStatus(results, assignment) {
  var status = { related_uploads: [] }
  results.forEach(result => {
    if (result.challenge_name === assignment && result.username === username) {
      status = result
    }
  })
  return status
}
export class ChallengePopUp extends React.Component {
  constructor(props) {
    super(props);
    this.state = { challenges: ["loading..."], uploading: false, results: [], update: false, challenges: [] }
    this.getChallengeResults()
    this.getChallenges()
  }
  getChallengeResults = async () => {
    var results = await getAssignmentResults()
    this.setState({ results: results, update: !this.state.update })
  }
  getChallenges = async () => {
    var challenges = await getChallengeInfo(this.props.downloadData.id)
    this.setState({ challenges: challenges })
  }
  renderChallenges() {


    //var lst = (this.props.downloadData.challenges ? this.props.downloadData.challenges : this.state.challenges)
    if (this.state.challenges.length == 0) {
      return <div>
        None
      </div>
    }
    return this.state.challenges.map(challenge => {
      var result = findAssignmentStatus(this.state.results, challenge.challenge_name)
      return (<div style={{ padding: "10px", borderRadius: "10px", cursor: "pointer", width: "100%", display: "flex", aligItems: "center" }} className="darken"
        onClick={() => {
          if (result.status === "approved") {
            this.setState({ feedback: true, currentChallenge: challenge })
          }
          this.setState({ uploading: true, currentChallenge: challenge })
        }}
      >
        <AssignmentStatus status={result.status} />
        <p1 style={{ marginLeft: "15px" }}>{challenge.challenge_name.split("\\n").map(challengeTextLine => {
          challengeTextLine += ` (${10*parseInt(challenge.point_multiplier)} points)`
          return <>{challengeTextLine} <br /></>
        })}</p1>
        <button className="add-task-button" style={{ marginLeft: "auto", backgroundColor: colorTheme.primaryColor}}><div className="add-button-text">{["submitted", "approved", "failed"].includes(result.status) ? "View Feedback" : "View Challenge"}</div></button>
      </div>)
    })
  }
  render() {
    if (this.state.feedback) {
      return <AssignmentFeedbackPopUp changePopUp={closePopup} currentChallenge={this.state.currentChallenge} assignmentResults={this.state.results}
        resubmit={() => this.setState({ uploading: true, feedback: false })} viewSubmission={() => this.setState({ viewSubmission: true })}
      />
    }
    if (this.state.uploading) {
      return <MasterPopUp changePopUp={closePopup} currentChallenge={this.state.currentChallenge} assignmentResults={this.state.results}
        viewOnly={this.state.viewSubmission}
      />
    }
    return (
      <div style={{ minWidth: "30vw", zIndex: 2, maxWidth: "70vw" }} key={this.state.update}>
        <div style={{ minWidth: "30vw", backgroundColor: colorTheme.primaryColor, opacity: "100%", zIndex: 2, borderRadius: "20px 20px 0 0 ", paddingBottom: "5px", paddingTop: "5px" }}>
          <button style={{ cursor: "pointer", float: "right", marginTop: "15px", marginRight: "15px" }} onClick={closePopup}>
            <CloseIcon color="white" /></button>
          <h1 style={{ marginLeft: "20px", color: "white" }}>
            <div style={{ marginRight: "15px", display: "inline-block" }}>
              <HatchetIcon size="40" colors={["white", colorTheme.primaryColor]} />
            </div>
            Challenges</h1></div>
        <div style={{ minWidth: "30vw", backgroundColor: "white", opacity: "100%", zIndex: 2, borderRadius: "0 0 20px 20px ", paddingBottom: "30px", paddingTop: "50px" }}>
          <div style={{ marginLeft: "30px", paddingRight: "30px" }}>
            {this.renderChallenges()}
            <div style={{ position: "relative", height: "50px" }}>
              {/*<div style={{ position: "absolute" }}><button style={{
                backgroundColor: "#965594", marginTop: "15px",
                color: "white", marginLeft: "10px", padding: "7px", borderRadius: "5px", cursor: "pointer"}}
                onClick={this.props.changePopUp}
              >Accept Challenges</button></div>*/}
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export function openInNewTab(href) {
  Object.assign(document.createElement('a'), {
    target: '_blank',
    href: href,
  }).click();
}


class MasterPopUp extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      challenges: ["loading..."],
      selectedFiles: [],
      uploadType: true,
      uploadURL: null
    }
    this.getPreviousSubmissions()
  }
  getPreviousSubmissions = async () => {
    var result = findAssignmentStatus(this.props.assignmentResults, this.props.currentChallenge.challenge_name)
    var selectedFiles = this.state.selectedFiles
    for (var i = 0; i < result.related_uploads.length; i++) {
      var id = result.related_uploads[i]
      var file = await getAssignmentById(id)
      if (file.url_submissions) {
        var url = file.url_submissions
      }
      else {
        var url = file.file
      }
      selectedFiles.push({ name: file.file_name, file: url, id: file.id })
    }
    this.setState({ selectedFiles: selectedFiles })
  }
  onChangeHandler = async event => {
    var selectedFiles = this.state.selectedFiles
    Object.keys(event.target.files).forEach(fileKey => {
      selectedFiles.push(event.target.files[fileKey])

      //Uploads file immediatly after selection
      //var file = event.target.files[fileKey]
      //uploadMasterAssignment(file, 1, file.name, null, this.props.currentChallenge.id)
    })
    this.setState({
      selectedFiles: selectedFiles, submitted: null, successfulUpload: null

    })

  }
  removeUploadFile = (index) => {
    var selectedFiles = this.state.selectedFiles
    selectedFiles.splice(index, 1)
    this.setState({ selectedFiles: selectedFiles })
  }
  uploadClickHandler = async () => {
    var successfulUpload = true;
    for (var i = 0; i < this.state.selectedFiles.length; i++) {
      var file = this.state.selectedFiles[i]
      if (!file) {
        return
      }
      if (!successfulUpload) {
        return
      }
      this.setState({ submitted: true })
      successfulUpload = await uploadMasterAssignment(file, 1, file.name, null, this.props.currentChallenge.id)


    }
    this.setState({ successfulUpload: successfulUpload })
    //this.props.changePopUp()
  }
  uploadLinksClickHandler = async () => {
    var successfulUpload = true;
    for (var i = 0; i < this.state.selectedFiles.length; i++) {
      var file = this.state.selectedFiles[i]
      if (!file.url) {
        return
      }
      if (!successfulUpload) {
        return
      }
      this.setState({ submitted: true })
      successfulUpload = await uploadMasterAssignment(null, 1, file.name, file.url, this.props.currentChallenge.id)
    }
    this.setState({ successfulUpload: successfulUpload })
    //this.props.changePopUp()
  }
  addLinkHandler = () => {
    var selectedFiles = this.state.selectedFiles
    selectedFiles.push({ name: this.state.uploadURL, url: this.state.uploadURL })
    this.setState({ selectedFiles: selectedFiles })
    //uploadMasterAssignment(null, 1, this.state.uploadURL, this.state.uploadURL, this.props.currentChallenge.id)

  }
  deleteFile = async (file, index) => {
    var fileInDatabase = file.id
    if (fileInDatabase) {
      fileInDatabase = !(await deleteSubmissionUpload(file.id))
    }
    if (!fileInDatabase) {
      var newSelectedFiles = this.state.selectedFiles
      newSelectedFiles.splice(index, 1)
      this.setState({ selectedFiles: newSelectedFiles })
    }
  }
  renderUploadField() {
    if (this.props.viewOnly) {
      return <></>
    }
    if (!this.state.uploadType) {
      return <><input type="file" multiple name="files[]" onChange={this.onChangeHandler} />
        <div style={{ marginTop: "20px" }}>
          <button style={{
            backgroundColor: colorTheme.primaryColor,
            color: "white", marginLeft: "10px", padding: "7px", borderRadius: "5px", cursor: "pointer"
          }}
            onClick={this.uploadClickHandler}

          >Submit as Group</button>
          <SubmissionLoader submitted={this.state.submitted} correct={this.state.successfulUpload} />
          {(this.props.viewOnly) ? null : <div style={{ display: "inline-block", marginLeft: "20px", transform: "translateY(10px)" }}>
            <UploadTypeSwitch uploadType={this.state.uploadType}
              changeUploadType={(uploadType) => this.setState({ uploadType: uploadType })}
            />

          </div>}
        </div></>
    }
    else {
      return <div>
        <input placeholder="URL" value={this.state.uploadURL} onChange={(text) => this.setState({ uploadURL: text.target.value, submitted: null, successfulUpload: null })}></input>
        <span>
          <button style={{
            backgroundColor: colorTheme.primaryColor,
            color: "white", marginLeft: "10px", padding: "7px", borderRadius: "5px", cursor: "pointer"
          }}
            onClick={this.addLinkHandler}

          >Add URL</button>
        </span>

        <div style={{ marginTop: "20px" }}>
          <button style={{
            backgroundColor: colorTheme.primaryColor,
            color: "white", marginLeft: "10px", padding: "7px", borderRadius: "5px", cursor: "pointer"
          }}
            onClick={this.uploadLinksClickHandler}

          >{this.props.currentChallenge.group_submission ? "Submit as Group" : "Submit as Student"}</button>
          <span>
            <SubmissionLoader submitted={this.state.submitted} correct={this.state.successfulUpload} />
          </span>
          {(this.props.viewOnly) ? null : <div style={{ display: "inline-block", marginLeft: "20px", transform: "translateY(10px)" }}>
            <UploadTypeSwitch
              uploadType={this.state.uploadType}
              changeUploadType={(uploadType) => this.setState({ uploadType: uploadType, submitted: null, successfulUpload: null })}
            />

          </div>}
        </div>
      </div>
    }
  }
  render() {
    return (
      <div style={{ zIndex: 2 }}>
        <div style={{ backgroundColor: colorTheme.primaryColor, opacity: "100%", zIndex: 2, borderRadius: "20px 20px 0 0 ", paddingBottom: "5px", paddingTop: "5px" }}>

          <button style={{ cursor: "pointer", float: "right", marginTop: "15px", marginRight: "15px" }} onClick={closePopup}>
            <CloseIcon color="white" /></button>
          <h1 style={{ marginLeft: "20px", color: "white" }}>
            {//<MasterIcon size="40" colors={["white","#965594"]/>
            }Submit</h1></div>
        <div style={{ minWidth: "30vw", backgroundColor: "white", opacity: "100%", zIndex: 2, borderRadius: "0 0 20px 20px ", paddingBottom: "50px", paddingTop: "30px", paddingRight: "30px" }}>
          <div style={{ position: "absolute", marginLeft: "30px" }}>
            <CashTracker height="40px"
              points={10 * parseInt(this.props.currentChallenge.point_multiplier)} />
          </div>
          <div style={{ position: "relative", marginLeft: "30px", display: "inline-block", marginTop: "60px" }}>
            {this.state.selectedFiles.map((file, index) => <div style={{ width: "100%" }}>
              {file.name}
              <div style={{ display: "inline-block", transform: "translateY(5px)", marginLeft: "30px", marginTop: "10px" }}>
                <button className="add-task-button" style={{backgroundColor: colorTheme.primaryColor}} onClick={() => {

                  openInNewTab(file.file)
                }}><div className="add-button-text">View</div></button>
                <button className="add-task-button" style={{ backgroundColor: "#e02424", marginLeft: "10px" }} onClick={() => {

                  this.deleteFile(file, index)
                }}><div className="add-button-text">Delete</div></button>
              </div>
            </div>)}
            {this.renderUploadField()}
          </div>

          <div style={{ display: "inline-block", marginLeft: "50px" }}>
            {this.props.currentChallenge.challenge_video_explanation ? <><div style={{ marginBottom: "20px", fontSize: "20px" }}>
              Challenge explanation:
            </div>
              {//<ReactPlayer url={this.props.currentChallenge.challenge_video_explanation} width="auto" height="100%" controls={true} />
              }<div>
                <JourneyVideoPlayer src={this.props.currentChallenge.challenge_video_explanation} />
              </div>
            </> :
              <>
                <div style={{ marginBottom: "20px", fontSize: "20px" }}>
                  Challenge Instructions:
                </div>
                <div style={{ width: "20vw" }}>
                  {this.props.currentChallenge.challenge_text_explanation}

                </div>
              </>}
            {true ? <button className="add-task-button" style={{ marginTop: "20px",backgroundColor: colorTheme.primaryColor }}
              onClick={
                () => {

                  if (this.props.currentChallenge.challenge_link_title.length == 1) {
                    openInNewTab(this.props.currentChallenge.challenge_link[0])
                    return
                  }
                  openPopup(<DownloadPopUp downloadData={{
                    download_titles: this.props.currentChallenge.challenge_link_title, download_urls:
                      this.props.currentChallenge.challenge_link
                  }} />)


                }
              }
            >
              <div className='add-button-text'>
                {(this.props.currentChallenge.challenge_link_title.length == 1) ? this.props.currentChallenge.challenge_link_title[0] : "Related resources"}
              </div>
            </button> : null}

          </div>

        </div>
      </div>


    )
  }
}

class UploadTypeSwitch extends React.Component {
  constructor() {
    super();
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(checked) {
    this.props.changeUploadType(checked)
    this.setState({ checked });
  }

  render() {
    // temporarily removed because file upload was disabled :(
    return <></>
    return (
      <label >
        <div style={{ display: "flex", alignItems: "center" }}>
          <Switch onChange={this.handleChange} checked={this.props.uploadType}
            uncheckedIcon={false}
            checkedIcon={false}
            height={30}
            width={60}
            onColor="#48E23D"
          />
          <span style={{ marginLeft: "10px" }}> File / URL</span>
        </div>
      </label>
    );
  }
}




