import React from 'react';
import "../style.css"
import { PointTrackers, MainPointTracker } from "../reused-components/pointTrackers.js";
import { AnswerIcon, MasterIcon } from '../reused-components/icons';
import { SideNav, Navbar } from "../reused-components/navigation.js";
import { buyReward, getAvailablePerksAndRewardsList, getUserPerksAndRewardsList, userCash } from '../backend';
import { isMobile } from 'react-device-detect';
import { closePopup, openPopup } from '../reused-components/Popup';
import { colorTheme } from '../reused-components/colorTheme';


export class PerksAndRewardsPage extends React.Component {
  constructor(props) {
    super(props)
    this.state = { isExtended: "extended" }
  }
  changeState = () => {
    if (this.state.isExtended === "extended") {
      this.setState({ isExtended: "not-extended" })
    }
    else {
      this.setState({ isExtended: "extended" })
    }
  }
  render() {
    return (
      <>
        <div style={{ position: "absolute" }}>
          <Navbar pageName="Perks and Rewards" sideNavAnimation={this.changeState} />
        </div>
        <div style={{ position: "absolute", zIndex: -1 }} className={this.state.isExtended}>
          <div style={{ display: "inline-block", position: "absolute" }}>
            <SideNav isExtended={"extended"} />
          </div>
          <div className="todo-outer-div" style={(isMobile ? { position: "fixed", width: "100vw", left: "-80vw", zIndex: -50 } : (this.state.isExtended === "extended") ? {} : { width: "100vw" })}>
            <div style={{ marginLeft: "40px" }}>
              <PointTrackers />
            </div>
            <div style={{ width: "calc(100% - 40px)", height: "calc(100% - 10vh)", marginTop: "5vh", marginLeft: "40px" }}>
              <PerksAndRewardsMainContainer />
            </div>
          </div>
          <div>
          </div>
        </div>
      </>
    )
  }
}




class PerksAndRewardsMainContainer extends React.Component {
  constructor(props) {
    super(props)
    this.state = { rewardsList: [], boughtRewards: [] }
    this.refreshRewardsList()
  }
  refreshRewardsList = async () => {
    var boughtRewards = await getUserPerksAndRewardsList()
    var rewardsList = await getAvailablePerksAndRewardsList()
    this.setState({ rewardsList: rewardsList, boughtRewards: boughtRewards })
  }
  render() {
    return <div style={{ height: "100%" }} key={JSON.stringify(this.state.rewardsList)}>{[
      this.state.rewardsList,
      this.state.rewardsList,
      this.state.rewardsList,
      //this.state.rewardsList
    ].map((item, index) => (
      <div style={{ width: "100%", height: "33%" }}>
        <PerksAndRewardsSubContainer content={item} index={index} boughtRewards={this.state.boughtRewards} />
      </div>)
    )}</div>
  }
}

class PerksAndRewardsSubContainer extends React.Component {
  constructor(props) {
    super(props)
    this.index = this.props.index
    this.state = { content: this.props.content, buyPopUpContent: "", buyPopUpShow: false, index: null, boughtBool: false }
  }
  changeBoughtState = (index) => {
    var newContent = this.state.content;
    newContent[index][0] = !newContent[index][0]
    this.changePopUp()
    this.setState({ content: newContent })
  }
  changePopUp = (index = null, boughtBool) => {
    this.setState({ buyPopUpShow: !this.state.buyPopUpShow, index: index, buyPopUpContent: this.state.content[index], boughtBool: boughtBool })


  }
  render() {

    return (
      <>

        <div style={{ borderBottom: "2px solid lightgray", height: "100%", maxHeight: "100%" }} className="perks-rewards-scrollable">
          <div style={{ height: "calc(30vh - 36px)" }}>
            {this.state.content.map((item, index) => {
              if (item.row_id !== this.props.index) {
                return null;
              }
              //var boughtBool = this.props.boughtRewards.includes(JSON.stringify(item.id))
              //alert(JSON.stringify(item))
              return (<div style={{ display: "inline-block", marginLeft: "40px", height: "100%", textAlign: "center", cursor: "pointer" }}
                onClick={() => {
                  openPopup(<BuyPopUp show={true} content={this.state.content[index]} index={index}
                  />)
                }}>
                {//<div style={{marginBottom: "-15px", marginTop: "5%"}}>{item.reward_name}</div>
                }
                <img height="47%" src={item.image} />
                <div style={{ height: "23%" }}>
                  <svg xmlns="http://www.w3.org/2000/svg" height="100%" width="auto" viewBox="0 0 124 50">
                    <rect id="Rectangle_1816" data-name="Rectangle 1816" width="124" height="50" rx="25" fill="#f8f8f8" />
                    <rect id="Rectangle_1817" data-name="Rectangle 1817" width="58" height="25" rx="11" transform="translate(50 12)" fill="#fff" />
                    <text id="_1817" data-name="1817" transform="translate(67 30)" stroke="#707070" stroke-width="1" font-size="13" font-family="SegoeUI, Segoe UI"><tspan x="0" y="0">1817</tspan></text>
                    <rect id="Rectangle_1818" data-name="Rectangle 1818" width="124" height="50" rx="13" fill="#f8f8f8" />
                    <path id="Polygon_15" data-name="Polygon 15" d="M24,0l8,16L24,32H8L0,16,8,0Z" transform="translate(44 7) rotate(90)" fill={colorTheme.primaryColor}/>
                    <rect id="Rectangle_1819" data-name="Rectangle 1819" width="58" height="25" rx="11" transform="translate(50 12)" fill="#fff" />
                    <text id="C" transform="translate(21 31)" fill="#fff" font-size="22" font-weight="700"><tspan x="0" y="0">C</tspan></text>
                    <text id="_1999" data-name="1999" transform="translate(62 31)" font-size="17" font-weight="600"><tspan x="0" y="0">{item.answer_points}</tspan></text>
                  </svg>
                </div>
                <div style={{ fontSize: "15px" }}>{item.reward_name}</div>
              </div>

              )
            })}
          </div>
        </div>

      </>

    )
  }
}

class BuyPopUp extends React.Component {
  render() {
    if (!this.props.show) {
      return <></>;
    }

    return (
      <div style={{
        backgroundColor: "white", zIndex: 2, padding: "20px", borderRadius: "20px", minHeight: "500px", minWidth: "400px",
        position: "relative", maxWidth: "90vw", paddingLeft: "40px"
      }}><div style={{ maxHeight: "400px" }}>

          <div style={{ display: "inline-block", height: "150px", width: "150px", borderRadius: 10000, overflow: "hidden" }}>
            <img src={this.props.content.image} heigth="100%" width="100%" alt="reward" />
          </div>
          <div style={{ display: "inline-block", fontWeight: "bold", position: "absolute", top: "80px", marginLeft: "20px" }}>
            {this.props.content.description_title}
            <div style={{ position: "absolute", marginTop: "10px" }}>
              <svg xmlns="http://www.w3.org/2000/svg" height="50px" width="auto" viewBox="0 0 124 50">
                <rect id="Rectangle_1816" data-name="Rectangle 1816" width="124" height="50" rx="25" fill="#f8f8f8" />
                <rect id="Rectangle_1817" data-name="Rectangle 1817" width="58" height="25" rx="11" transform="translate(50 12)" fill="#fff" />
                <text id="_1817" data-name="1817" transform="translate(67 30)" stroke="#707070" stroke-width="1" font-size="13" font-family="SegoeUI, Segoe UI"><tspan x="0" y="0">1817</tspan></text>
                <rect id="Rectangle_1818" data-name="Rectangle 1818" width="124" height="50" rx="13" fill="#f8f8f8" />
                <path id="Polygon_15" data-name="Polygon 15" d="M24,0l8,16L24,32H8L0,16,8,0Z" transform="translate(44 7) rotate(90)" fill={colorTheme.primaryColor} />
                <rect id="Rectangle_1819" data-name="Rectangle 1819" width="58" height="25" rx="11" transform="translate(50 12)" fill="#fff" />
                <text id="C" transform="translate(21 31)" fill="#fff" font-size="22" font-weight="700"><tspan x="0" y="0">C</tspan></text>
                <text id="_1999" data-name="1999" transform="translate(62 31)" font-size="17" font-weight="600"><tspan x="0" y="0">{this.props.content.answer_points}</tspan></text>

              </svg></div></div>
          <div style={{ maxHeight: "240px", marginTop: "20px" }} className="scrollingThing">

            <div style={{ lineHeight: 1.5 }}>{(this.props.content.description).split("\r\n").map(item => <div style={{ height: "20px" }}>{item}</div>)}</div>
          </div>
        </div>

        <div style={{ marginBottom: "20px", marginTop: "30px", padding: "30px", borderRadius: "20px", backgroundColor: "#f8f8f8", width: "300px" }}>
          Do you want to confirm your purchase?
          <div style={{ marginTop: "20px" }}>
            <button onClick={async () => {
              var purchaseResult = await buyReward(this.props.content.id)
              openPopup(<div style={{ padding: "20px", borderRadius: "20px", backgroundColor: "white", zIndex: 2 }}>

                {purchaseResult}
                <div style={{ marginTop: "10px" }}>
                  <button
                    className="add-task-button"
                    onClick={() => {

                      closePopup()
                    }}
                  >
                    <div className="add-button-text">Okay</div>
                  </button>
                </div>
              </div>)
            }} className="add-task-button" style={{ backgroundColor: "#30e024" }}>
              <p1 className="add-button-text">Confirm</p1>
            </button>
            <button onClick={closePopup} className="cancel-button" style={{ backgroundColor: "#e02424" }}><p1 className="add-button-text">Cancel</p1></button>
          </div>
        </div>
      </div>)
  }
}




class StarIcon extends React.Component {
  render() {
    return (
      <svg xmlns="http://www.w3.org/2000/svg" height="58" viewBox="0 0 24 24" width="auto" fill={(this.props.bought ? "#000000" : "#717171")}><path d="M0 0h24v24H0z" fill="none" /><path d="M0 0h24v24H0z" fill="none" /><path d="M12 17.27L18.18 21l-1.64-7.03L22 9.24l-7.19-.61L12 2 9.19 8.63 2 9.24l5.46 4.73L5.82 21z" /></svg>
    )
  }
}

class RewardIcon extends React.Component {
  render() {
    return (<img src={this.props.imgUrl} style={{ height: "58px", width: "58px", borderRadius: 10000, opacity: (this.props.bought ? 1 : 0.5) }} alt="reward with opacity" />)
  }
}