import React from "react";
import { getViewPromptAnswers } from "../../backend.js";
import { colorTheme } from "../../reused-components/colorTheme.js";
import { CloseIcon, ReturnIcon, SkipBackwardIcon } from "../../reused-components/icons.js";
import { closePopup } from "../../reused-components/Popup.js";
import { AssignmentStatus } from "./ChallengePopUp.js";

export class ViewPromptForDiscussionPopUp extends React.Component {
    constructor(props) {
        super(props)
        this.state = { answers: [] }
        this.updatePromptAnswers()
    }
    updatePromptAnswers = async () => {
        var answers = await getViewPromptAnswers(this.props.promptID)
        /*
        Extended list to check how the popup looked
        for (var i = 0; i < 5; i++) {
            answers = answers.concat(answers)
        }*/
        this.setState({ answers: answers })
    }
    renderPromptAnswers() {
        if (this.state.expandedAnswer) {
            return <div style={{width: "90vw", maxWidth: "500px",}}>
                {this.state.expandedAnswer}
            </div>
        }
        return <div style={{maxHeight: "calc(80vh - 250px)", overflowX: "hidden"}} className="scrollingThing">
            {this.state.answers.map(answer => 
            <div style={{ width: "90vw", maxWidth: "500px", maxHeight: "37px", overflow: "hidden", borderRadius: "10px", padding: "10px", cursor: "pointer",position: "relative"}}
                className="dropdown-menu-item"
                onClick={() => {
                    this.setState({expandedAnswer: answer.prompt_answer})
                }}
            >
                <div style={{ position: "absolute" }}><AssignmentStatus status={answer.status} /></div>
                <div style={{ marginLeft: "50px" }}>{answer.prompt_answer}
                </div>

            </div>)}</div>
    }
    render() {
        return <div style={{ minWidth: "30vw", zIndex: 2, maxWidth: "90vw" }}>
            <div style={{ minWidth: "30vw", backgroundColor: colorTheme.primaryColor, opacity: "100%", zIndex: 2, borderRadius: "20px 20px 0 0 ", paddingBottom: "5px", paddingTop: "5px" }}>
                <button style={{ cursor: "pointer", float: "right", marginTop: "15px", marginRight: "15px" }} onClick={() => {
                    closePopup()
                }}>
                    <CloseIcon color="white" /></button>
                <h1 style={{ marginLeft: "20px", color: "white" }}>Discuss Answers</h1></div>
            <div style={{ minWidth: "30vw", backgroundColor: "white", opacity: "100%", zIndex: 2, borderRadius: "0 0 20px 20px ", paddingTop: "10px", paddingBottom: "20px" }}>
                <div style={{ margin: "40px", marginTop: 0 }}>
                    <h1>
                        {this.state.expandedAnswer? 
                        <div onClick={() => this.setState({expandedAnswer: null})} style={{cursor: "pointer"}}>
                            <ReturnIcon size="24px" />
                        </div>
                        :null}{this.props.promptQuestion}
                    </h1>
                    {this.renderPromptAnswers()}
                </div>

            </div>
        </div>
    }
}