import { AssignmentStatus } from "../Pages/HomePage/ChallengePopUp.js";
import { updateStudentAssignmentAppJS } from "../App.js";
import { QuestionIcon } from "./icons";
import { closePopup, openPopup } from "./Popup";
import Slider from 'react-input-slider';


