//Used to determine student and assignment in TeacherSingleAssignmentOverview
export var currentStudent;
export var currentAssignment;
export var currentSubmissionData;
export var currentChallengeID
export var currentSubmissionList;
export var currentSubmissionListID
export var currentPromptID

export function increaseCurrentSubmissionListID() {
  currentSubmissionListID++;
}

export class TeacherOverviewList extends React.Component {
  constructor(props) {
    super(props)
    this.type = this.props.type
    this.state = { overviewList: [], checkpoints: checkpointList }
    if (this.type === "student") {
      this.updateStudentList()
      this.icon = <StudentIcon />
    }
    else if (this.type === "assignment") {
      //this.updateAssignmentList()
      this.icon = <AssignmentIcon />
    }
    else if (this.type === "lesson") {
      //this.updateLessonList()
      this.icon = <AssignmentIcon />
    }
    else if (this.type === "studentPrompt") {
      this.updateStudentPromptSubmissionList()
      this.icon = <StudentIcon />
    }
    else if (this.type === "group") {
      this.updateClassGroupList()
      this.icon = <StudentIcon />
    }
  }
  renderRightField = (item) => {
    if (this.type === "assignment") {
      return;
      //Not needed atm, was just used earlier for clarifying the difference between assignment and student
      return <div style={{ position: "absolute", right: "5%" }}>Due 15:00 21/10</div>
    }

    else if (this.props.showMissed) {
      return
      return <div style={{ position: "absolute", right: "5%" }}>0</div>
    }

  }
  updateClassGroupList = async () => {
    var groupList = await getClassGroups()
    this.setState({ overviewList: groupList })
  }
  updateStudentPromptSubmissionList = async () => {
    var studentList = await getClassInfo()
    var challengeResults = []
    if (currentChallengeID) {

      challengeResults = await getViewPromptAnswers(currentPromptID)
    }
    var submittedStudentsList = []
    challengeResults.forEach(student => {

      submittedStudentsList.push(student.student)
    })
    studentList.forEach(student => {
      if (!submittedStudentsList.includes(student)) {
        challengeResults.push({ student: student })
      }
    })
    var newOverviewList = challengeResults.map(student => {
      student.username = student.student
      return student
    })
    this.setState({ overviewList: newOverviewList })
  }
  updateAssignmentList = async (id) => {
    this.type = "assignment"
    var assignmentList = await getAssignmentNames(id);
    var promptList = await getPromptsThroughLessonId(id)
    this.setState({ overviewList: assignmentList, promptList: promptList })
  }
  updateLessonList = async (checkpointID) => {
    var lessonList = await getLessonNamesThroughCheckpoint(checkpointID)
    this.setState({ overviewList: lessonList })
  }
  updateStudentList = async () => {
    var studentList = await getClassInfo()
    var challengeResults = []
    if (currentChallengeID) {

      challengeResults = await getChallengeSubmissions(currentChallengeID)
    }
    var submittedStudentsList = []
    challengeResults.forEach(student => {
      submittedStudentsList.push(student.username)
    })
    studentList.forEach(student => {
      if (!submittedStudentsList.includes(student)) {
        challengeResults.push({ username: student })
      }
    })
    this.setState({ overviewList: challengeResults })
  }
  updateStudentAssignment = async (item) => {
    var tempState = { checkpoint: this.state.checkpointIndex + 1 }
    tempState[this.type] = item


    this.setState(tempState)

  }
  updateChallengeSubmissions = async (challengeID) => {
    var challengeResults = []
    challengeResults = await getChallengeSubmissions(challengeID)
    this.setState({ challengeResults: challengeResults })
  }
  render() {
    var suffix = ""
    if (this.props.suffix) {
      suffix = this.props.suffix
    }
    if (this.type === "lesson") {
      return (<div className="teacher-overview-outer-div" style={{ height: "50vh", cursor: (this.props.notClickable ? "default" : "pointer") }}>
        <div>
          {this.state.checkpoints.map((checkpoint, checkpointIndex) =>
            <div className={(this.state.checkpointIndex === checkpointIndex) ? "checkpoint-portfolio-download" : "checkpoint-portfolio-download-closed"} style={{ backgroundColor: "transparent" }}>
              <div className="teacher-overview-item" style={{ cursor: (this.props.notClickable ? "default" : "pointer") }}
                onClick={async () => {
                  if (this.props.notClickable) {
                    return
                  }
                  await this.updateLessonList(checkpointIndex)
                  this.type = "lesson"
                  this.setState({ checkpointIndex: checkpointIndex })
                }}
              ><div style={{ fontWeight: 600, fontSize: "3vh", marginLeft: "1vw" }}

              >{checkpoint}</div></div>
              {(this.state.checkpointIndex === checkpointIndex) ? this.state.overviewList.map((item) => {
                return (
                  <div className="teacher-overview-item" style={{ marginLeft: "40px", cursor: (this.props.notClickable ? "default" : "pointer") }} onClick={() => {
                    if (this.props.notClickable) {
                      return
                    }
                    var lesson = item
                    this.updateAssignmentList(lesson.id)
                    //this.setState({ overviewList: lesson.challenges })
                    this.type = "assignment"
                    this.icon = <AssignmentIcon />
                    //this.updateStudentAssignment(item.name)
                  }}>
                    {this.icon} <div style={{ fontWeight: 600, fontSize: "3vh", marginLeft: "1vw" }}>{item.name}</div> {this.renderRightField(item.name)}</div>)
              }) : null}</div>)} </div>
      </div>)
    }
    if (this.type === "assignment") {
      return (<div className="teacher-overview-outer-div" style={{ height: "50vh", cursor: (this.props.notClickable ? "default" : "pointer") }}>
        <div>
          {this.state.checkpoints.map((checkpoint, checkpointIndex) =>
            <div className={(this.state.checkpointIndex === checkpointIndex) ? "checkpoint-portfolio-download" : "checkpoint-portfolio-download-closed"} style={{ backgroundColor: "transparent" }}>
              <div className="teacher-overview-item" style={{ cursor: (this.props.notClickable ? "default" : "pointer") }}
                onClick={async () => {
                  if (this.props.notClickable) {
                    return
                  }
                  await this.updateLessonList(checkpointIndex)
                  this.type = "lesson"
                  this.setState({ checkpointIndex: checkpointIndex })
                }}
              ><div style={{ fontWeight: 600, fontSize: "3vh", marginLeft: "1vw" }}

              >{checkpoint}</div></div>
              {(this.state.checkpointIndex === checkpointIndex) ? [this.state.overviewList.map((item) => {
                return (
                  <div onClick={() => {
                    if (this.props.notClickable) {
                      return
                    }
                    currentAssignment = item.challenge_name
                  }}>
                    <Link style={{ color: "black", textDecoration: "none" }} to={this.props.link + this.type}>
                      <div className="teacher-overview-item" style={{ marginLeft: "40px", cursor: (this.props.notClickable ? "default" : "pointer") }} onClick={() => {

                        currentChallengeID = item.id
                        this.updateStudentAssignment(item)
                      }}>
                        {this.icon} <div style={{ fontWeight: 600, fontSize: "3vh", marginLeft: "1vw" }}>{item.challenge_name}</div> {this.renderRightField(item)}</div>
                    </Link></div>
                )
              }),
              this.state.promptList.map((item) => {
                return (
                  <div onClick={() => {
                    if (this.props.notClickable) {
                      return
                    }
                    currentPromptID = item.id
                  }}>
                    <Link style={{ color: "black", textDecoration: "none" }} to={this.props.link + "prompt"}>
                      <div className="teacher-overview-item" style={{ marginLeft: "40px", cursor: (this.props.notClickable ? "default" : "pointer") }} onClick={() => {

                        currentChallengeID = item.id
                        this.updateStudentAssignment(item)
                      }}>
                        <span style={{ height: "24px", width: "24px" }}>{<QuestionIcon />}</span> <div style={{ fontWeight: 600, fontSize: "3vh", marginLeft: "1vw" }}>{item.title}</div> {this.renderRightField(item)}</div>
                    </Link></div>
                )
              })
              ] : null}</div>)} </div>
      </div>)
    }


    else if (this.type === "student" || this.type === "studentPrompt") {
      console.log(this.state.overviewList)
      return (<div className="teacher-overview-outer-div" key={JSON.stringify(this.state.challengeResults)} >
        {this.state.overviewList.map((item, index) => {

          return (<div onClick={() => {
            if (this.props.notClickable) {
              return
            }
            currentStudent = item.username
            currentSubmissionData = item
            currentSubmissionList = this.state.overviewList
            currentSubmissionListID = index
          }}><Link style={{ color: "black", textDecoration: "none" }} to={item.status ? this.props.link + this.type : null}>
              <div className="teacher-overview-item" style={{ cursor: (this.props.notClickable ? "default" : "pointer") }} onClick={() => this.updateStudentAssignment(item)}>
                {this.icon}
                <div style={{ fontWeight: 600, fontSize: "3vh", marginLeft: "1vw" }}>
                  {item.username}
                </div> {this.renderRightField(item)}
                <div style={{ position: "absolute", right: "5%" }}>
                  {(item.status && (this.props.showStatus)) ? <AssignmentStatus status={item.status} /> : null}
                  {this.props.givePoints ? <GivePointButton receiverName={item.username} studentOrGroup={"student"}/> : null}

                </div>
              </div>
            </Link></div>
          )
        })}
      </div>)
    }
    else if (this.type === "group") {
      console.log(this.state.overviewList)
      return (<div className="teacher-overview-outer-div" key={JSON.stringify(this.state.challengeResults)} style={{ cursor: (this.props.notClickable ? "default" : "pointer") }}>
        {this.state.overviewList.map((item, index) => {

          return (<div onClick={() => {
            if (this.props.notClickable) {
              return
            }
            currentStudent = item.username
            currentSubmissionData = item
            currentSubmissionList = this.state.overviewList
            currentSubmissionListID = index
          }}><Link style={{ color: "black", textDecoration: "none", cursor: "default" }} to={item.status ? this.props.link + this.type : null}>
              <div className="teacher-overview-item" style={{ cursor: (this.props.notClickable ? "default" : "pointer") }} onClick={() => this.updateStudentAssignment(item)}>
                {this.icon}
                <div style={{ fontWeight: 600, fontSize: "3vh", marginLeft: "1vw" }}>
                  {item}
                </div> {this.renderRightField(item)}
                <div style={{ position: "absolute", right: "5%" }}>
                  {(item.status && (this.props.showStatus)) ? <AssignmentStatus status={item.status} /> : null}
                  {this.props.givePoints ? <GivePointButton receiverName={item} studentOrGroup={"group"}/> : null}
                </div>
              </div>
            </Link></div>
          )
        })}
      </div>)
    }
  }
}
