export class StudentIcon extends React.Component {
  render() {
    return (
      <div style={{ height: "60%", display: "inline-block" }}>
        <svg id="account_circle_black_24dp" xmlns="http://www.w3.org/2000/svg" width="auto" height="100%" viewBox="0 0 50 50">
          <path id="Path_27" data-name="Path 27" d="M0,0H50V50H0Z" fill="none" />
          <path id="Path_28" data-name="Path 28" d="M22.833,2A20.833,20.833,0,1,0,43.667,22.833,20.841,20.841,0,0,0,22.833,2Zm0,6.25a6.25,6.25,0,1,1-6.25,6.25A6.242,6.242,0,0,1,22.833,8.25Zm0,29.583a15,15,0,0,1-12.5-6.708c.063-4.146,8.333-6.417,12.5-6.417,4.146,0,12.437,2.271,12.5,6.417A15,15,0,0,1,22.833,37.833Z" transform="translate(2.167 2.167)" />
        </svg>
      </div>
    )
  }
}

class GivePointButton extends React.Component {
  render() {
    return <div style={{ height: "30px", cursor: "pointer" }} onClick={
      () => {
        openPopup(<GivePointPopup receiverName={this.props.receiverName} studentOrGroup={this.props.studentOrGroup}/>)
      }
    }>
      <svg xmlns="http://www.w3.org/2000/svg" height="100%" viewBox="0 0 24 24" width="auto" fill="#000000">
        <path d="M0 0h24v24H0z" fill="none" />
        <path d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm5 11h-4v4h-2v-4H7v-2h4V7h2v4h4v2z" />
      </svg>
    </div>
  }
}


class GivePointPopup extends React.Component {
  constructor(props) {
    super(props)
    this.state = { points: 5 }
  }
  render() {
    return <div style={{ paddingBottom: "30px", backgroundColor: "white", zIndex: 2, borderRadius: "30px", paddingLeft: "60px", paddingRight: "60px", paddingTop: "20px"}}>
      <h1 style={{marginBottom: "40px"}}>
        {this.props.receiverName}
      </h1>
      <AssignmentPointsSlider points={this.state.points} changePoints={points => this.setState({ points: points })} />
      <button className="add-task-button" style={{backgroundColor: "#30e024", marginTop: "30px"}} onClick={
        () => {
          givePoints(this.state.points, this.props.receiverName, this.props.studentOrGroup)
          closePopup()        
        }
      }>
        <div className="add-button-text">
          Give Points
        </div>
      </button>
    </div>

  }
}


class AssignmentPointsSlider extends React.Component {
  constructor(props) {
    super(props)
    this.state = { pointValue: this.props.points * 10 }
  }
  render() {
    return (
      <div style={{ position: "relative" }}>
        <div style={{ fontWeight: 600, fontSize: 20 }}>
          Points ({this.props.points}/10)
        </div>
        <div style={{ marginTop: "20px" }}>
          <Slider
            axis="x"
            styles={{
              track: { width: 300, height: 25, borderRadius: 40 },
              active: { borderRadius: 40 },
              thumb: { width: 30, height: 30 }
            }}
            x={this.state.pointValue}
            onChange={({ x }) => {
              this.props.changePoints(Math.floor(x / 10))
              this.setState({ pointValue: x })
            }}
          /></div>
      </div>

    )
  }
}